.about-us {
  background: #192144 url("../img/about-mobile@1x.png") no-repeat center bottom;
  position: relative;
  background-size: auto;
  padding-top: 50px;
  padding-bottom: 47px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  z-index: 6;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/about-mobile@2x.png");
  }
}

.about-us__title {
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: normal;
  text-align: left;
  color: #ff8d30;
  padding-left: 25px;
  margin-top: 22px;
  margin-bottom: 20px;
}

.about-us__text {
  line-height: 1.38;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  padding: 0 35px 0 25px;
}

.about-us__bold {
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.38;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  padding: 0 25px;
}

.about-us__to-all {
  display: none;
}

@media (min-width: $tablet) {
  .about-us {
    background: #192144 url("../img/about-tablet@1x.png") 385px 284px no-repeat;
    padding-top: 210px;
    padding-bottom: 88px;
    margin-top: -140px;
    border-bottom-right-radius: 60px;
    border-bottom-left-radius: 60px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/about-tablet@2x.png");
    }
  }

  .about-us__title {
    font-size: 45px;
    width: 688px;
    border-radius: 20px;
    padding-left: 34px;
    box-sizing: border-box;
    margin-bottom: 40px;
    margin-top: 64px;
  }

  .about-us__text {
    font-size: 28px;
    width: 360px;
    line-height: 1.18;
    padding: 0 22px 0 36px;
    margin-top: 0;
    margin-bottom: 33px;
  }

  .about-us__bold {
    font-size: 21px;
    width: 360px;
    margin-bottom: 50px;
    padding: 0 22px 0 36px;
    line-height: 1.19;
  }

  .about-us__to-all {
    display: block;
    position: relative;
    border-radius: 20px;
    font-size: 24px;
    text-align: left;
    padding: 20px 34px;
    text-transform: none;
    margin-top: 55px;
    margin-left: 36px;
    height: 81px;
    width: 274px;

    &::before {
      content: "";
      position: absolute;
      top: 34px;
      right: 36px;
      background: url("../img/triangle-arrow-right.svg") center no-repeat;
      width: 10px;
      height: 14px;
    }
  }
}

@media (min-width: $desktop) {
  .about-us {
    padding: 213px 132px 162px;
    margin-top: -50px;
    background: #192144 url("../img/about-desktop@1x.png") 750px 100% no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/about-desktop@2x.png");
    }
  }

  .about-us__title {
    padding-left: 0;
    margin-bottom: 59px;
  }

  .about-us__text {
    padding: 0;
    width: 630px;
    margin-bottom: 41px;
  }

  .about-us__bold {
    padding: 0;
    width: 410px;
  }

  .about-us__to-all {
    margin-left: 0;
    margin-top: 87px;
  }
}
